import { createWebHistory, createRouter } from "vue-router";
import Product from './components/ProductWrap.vue'
import Service from './components/ServiceWrap.vue'
import Company from './components/CompanyWarp.vue'
import Iu from './components/IuWrap.vue'

const routes = [
    {
        path: '/', 
        component: Product
    },
    {
        path: '/service',
        component: Service
    },
    {
        path: '/Company',
        component: Company
    },
    {
        path: '/Iu',
        component: Iu
    }
]
const router = createRouter({
    history: createWebHistory(), routes,
   });
   export default router;     